import { Component, OnInit } from '@angular/core';
import { faChevronRight, faUser, faKey, faAddressCard, faStopwatch, faPlus, faEllipsisV, faEdit, faTrash } from '@fortawesome/free-solid-svg-icons';
import { AuthService } from '../services/auth.service';
import { environment } from '../../environments/environment';
import { HttpService } from '../services/http.service';
import {
  NgbModal,
  NgbModalOptions
} from '@ng-bootstrap/ng-bootstrap';
import { NgForm } from '@angular/forms';
import { DataService } from '../services/data.service';

@Component({
  selector: 'app-profile',
  templateUrl: './profile.component.html',
  styleUrls: ['./profile.component.scss']
})
export class ProfileComponent implements OnInit {

  faChevronRight = faChevronRight;
  faUser = faUser;
  faAddressCard = faAddressCard;
  faKey = faKey;
  faStopwatch = faStopwatch;
  faPlus = faPlus;
  faEllipsisV = faEllipsisV;
  faEdit = faEdit;
  faTrash = faTrash;

  tabs = { personal: false, address: true, password: false };
  timeLeft = environment.OTP_TIMEOUT;
  timerText: string;
  timeTracker: any;
  OTP: string;
  timerExpired = false;
  timerView = false;
  modalReference: any;
  addressArr: any = [];

  // personalForm Fields
  firstName: string = '';
  lastName: string = '';
  emailID: string = '';
  mobileNumber: string = '';

  // resetPasaswordForm Fields
  resetPwdForm = { currentPassword: '', newPassword: '', confirmPassword: '' }

  // addressForm Fields
  addressMode: string = 'NEW';
  modalTitle: string = 'New Address';
  modalBtnText: string = 'Submit';
  userName: string = '';
  addressMobileNumber: string = '';
  pinCode: string = '';
  addressText: string = '';
  cityName: string = '';
  stateName: string = 'Maharashtra';
  landmark: string = '';
  addressType: string = '';
  addressID: number;

  constructor(public authService: AuthService, private _httpService: HttpService, private modalService: NgbModal, public dataService: DataService) { }

  ngOnInit(): void {
    this.firstName = this.authService.usrObj.firstname;
    this.lastName = this.authService.usrObj.lastname;
    this.emailID = this.authService.usrObj.emailid;
    this.mobileNumber = this.authService.usrObj.mobilenumber;
    this.fetchAddress();
  }
  // ========================================================================================================
  activateTab(name: string) {
    switch (name) {
      case 'Personal': this.tabs = { personal: true, address: false, password: false }; break;
      case 'Address': this.tabs = { personal: false, address: true, password: false }; break;
      case 'Password': this.tabs = { personal: false, address: false, password: true }; break;
    }
  }
  // ========================================================================================================
  resetPassword() {
    let body = {
      oldPassword: this.resetPwdForm.newPassword,
      newPassword: this.resetPwdForm.confirmPassword
    }
    this._httpService.postRequest(environment.RESET_PWD, JSON.stringify(body), this.authService.token).subscribe((res: any) => {
      let body = res.body;
    }, (error: Error) => {
      this.dataService.handleErrorResponse(error);
    });
  }
  // ========================================================================================================
  launchModal(ref: any, action: string, index?: number) {
    const ngbModalOptions: NgbModalOptions = {
      backdrop: 'static',
      keyboard: false,
      size: 'lg'
    };
    this.addressMode = action;
    if (action === 'EDIT') {
      this.modalTitle = 'Edit Address';
      this.modalBtnText = 'Update';
      this.userName = this.addressArr[index].name;
      this.addressMobileNumber = this.addressArr[index].mobilenumber;
      this.pinCode = this.addressArr[index].pincode;
      this.addressText = this.addressArr[index].address;
      this.cityName = this.addressArr[index].city;
      this.stateName = 'Maharashtra';
      this.landmark = this.addressArr[index].landmark;
      this.addressType = this.addressArr[index].type.charAt(0).toUpperCase() + this.addressArr[index].type.slice(1);
      this.addressID = this.addressArr[index].addressid;
    } else {
      this.modalTitle = 'New Address';
      this.modalBtnText = 'Submit';
    }
    this.modalReference = this.modalService.open(ref, ngbModalOptions);
    this.modalReference.result.then(
      (result: string) => { },
      (reason: any) => {
      }
    );
  }
  // ========================================================================================================
  submit(obj: NgForm) {
    let formValues = obj.form.value;
    let body = {
      name: formValues.userName,
      mobile_number: formValues.addressMobileNumber,
      pincode: formValues.pinCode,
      address: formValues.addressText,
      city: formValues.cityName,
      state: formValues.stateName,
      landmark: formValues.landmark,
      type: formValues.addressType,
    }
    if (this.addressMode === 'NEW') {
      this._httpService.postRequest(environment.USER_ADDRESS, JSON.stringify(body), this.authService.token).subscribe((res: any) => {
        let body = res.body;
        this.modalService.dismissAll();
      }, (error: Error) => {
        this.dataService.handleErrorResponse(error);
      });
    } else {
      body['addressId'] = this.addressID;
      this._httpService.putRequest(environment.USER_ADDRESS, JSON.stringify(body), this.authService.token).subscribe((res: any) => {
        let body = res.body;
        this.modalService.dismissAll();
      }, (error: Error) => {
        this.dataService.handleErrorResponse(error);
      });
    }
  }
  // ========================================================================================================
  validate(evt: any) {
    var charCode = (evt.which) ? evt.which : evt.keyCode
    if (charCode > 31 && (charCode < 48 || charCode > 57))
      return false;
    return true;
  }
  // ========================================================================================================
  fetchAddress() {
    this._httpService.getRequest(environment.USER_ADDRESS, this.authService.token).subscribe((res: any) => {
      let body = res.body;
      this.addressArr = body.address;
    }, (error: Error) => {
      this.dataService.handleErrorResponse(error);
    });
  }
  // ========================================================================================================
  editAddress(index: number) {
    let body = this.addressArr[index];
  }
  // ========================================================================================================
  deleteAddress(addressId: string) {
    this._httpService.deleteRequest(environment.USER_ADDRESS + "?addressId=" + addressId, this.authService.token).subscribe((res: any) => {
      let body = res.body;
      this.fetchAddress();
    }, (error: Error) => {
      this.dataService.handleErrorResponse(error);
    });
  }
  // ========================================================================================================
  ngOnDestroy(): void {
    delete this.timeLeft;
    delete this.timerText;
    delete this.timeTracker;
    delete this.timerView;
    delete this.tabs;
    delete this.OTP;
    delete this.timerExpired;
    delete this.timerView;
    delete this.modalReference;
    delete this.addressArr;

    delete this.firstName;
    delete this.lastName;
    delete this.emailID;
    delete this.mobileNumber;
    delete this.resetPwdForm;

    delete this.addressMode;
    delete this.modalTitle;
    delete this.modalBtnText;
    delete this.userName;
    delete this.addressMobileNumber;
    delete this.pinCode;
    delete this.addressText;
    delete this.cityName;
    delete this.stateName;
    delete this.landmark;
    delete this.addressType;
    delete this.addressID;

    delete this.faStopwatch;
    delete this.faAddressCard;
    delete this.faChevronRight;
    delete this.faEdit;
    delete this.faEllipsisV;
    delete this.faKey;
    delete this.faPlus;
    delete this.faTrash;
    delete this.faUser;
  }
  // ========================================================================================================
}
